import { Component, OnInit, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';//事项类型的接口
import { ActivatedRoute, Router } from '@angular/router';
import { BtnPageBean } from '../bean/btnPage.bean';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import * as _ from 'lodash';
import * as $ from 'jquery';
/**
 * 提醒事项
 */
@Component({
  selector: 'app-tip-page',
  templateUrl: './tip-page.component.html',
  styleUrls: ['./tip-page.component.scss',
    './../menu-tree-ul.component.scss'
  ],
  providers: [CusOperationHttpService, CustomeHttpService, Commfunc]
})
export class TipPageComponent implements OnInit, OnChanges {
  partyOrGroupNo: any;
  bussinessWhere: any;
  disinNav: boolean = false;
  inNav: any;
  ulData: any = [];
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  littleEvent: any = [];
  scheduleTypeParentId: string;
  btnPageBean: BtnPageBean = new BtnPageBean();
  itemsSmallType: any = [
  ];
  list: any = [];//接收后台数据
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: number;
  //每页多少条
  pageSize: number;
  pageNum: number;
  userId: string = '';
  display: boolean = false;
  showModel: string;
  inValue: any;

  //获取所有码值
  code: any;
  flag: string;
  constructor(
    public router: Router,
    public cusOperationHttpService: CusOperationHttpService,
    public customeHttpService: CustomeHttpService,
    public activatedRouter: ActivatedRoute,
    public commfunc: Commfunc,
    public httpService: CusOperationHttpService,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.littleEvent = this.code['ScheduleType'];
  }

  ngOnInit() {
    let url = window.location.href
    if (url.indexOf('?') != -1) {
      let a = url.split('?');
      let b = a[1].split('&');
      let c = b[0].split('=');
      this.flag = c[1];
    }
    let session = window.sessionStorage;
    if (session.getItem('bussinessWhere')) {
      let bussinessWhere = session.getItem('bussinessWhere');
      this.bussinessWhere = bussinessWhere.split('or');
      this.partyOrGroupNo = this.bussinessWhere[0].replace(/[^0-9]/ig, '');
    }
    this.itemsBigChange();
    this.queryCopReminderTaskSum();
  }

  ngOnChanges() {
    this.ngOnInit()
  }
  //事项大类切换
  itemsBigChange() {
    let params = {
      taskTypeParentId: '01',
      taskTypeKey: 'itemsType'
    }
    this.cusOperationHttpService.taskTypeQry(params).subscribe(data => {
      let arr = data.taskTypeList;
      this.itemsSmallType = [{ label: '请选择', value: '' }];
      if (arr) {
        arr.forEach(item => {
          this.itemsSmallType.push({ label: item.taskTypeName, value: item.taskTypeId });
        });
      }
    })
  }
  //点击处理按钮
  toDisposeSchedule(item) {
    // this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page3/reminders', { scheduleTypeId: item.scheduleTypeId, scheduleName: item.scheduleName }], { queryParams: { flag: this.flag, 'id': '4' } });
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //提醒事项
  queryCopReminderTaskSum() {
    let param = {};
    if (this.flag == '4') {
      param = { statusId: '1' }
    }
    if (this.partyOrGroupNo) {
      param = {
        statusId: '1',
        custId: this.partyOrGroupNo
      }
    }
    let ob = this.customeHttpService.queryCopReminderTaskSum(param);
    let approvelist = []
    ob.subscribe(data => {
      this.list = data.resultList;
      if (this.list && this.list.length > 0) {
        for (let i = 0; i < this.list.length; i++) {
          let element = this.list[i];
          let filterResult = approvelist.filter(x => x.scheduleTypeId == element['scheduleTypeId']);
          let exist = filterResult && filterResult.length;
          let temp = null;
          if (exist) {
            temp = {
              countNum: element['countNum'],
              createDate: element['createDate'],
              isNew: element['isNew'],
              scheduleName: element['scheduleName'],
              scheduleTypeId: element['scheduleTypeId'],
              statusId: element['statusId'],
              subSysId: element['subSysId'],
              taskCategoryId: element['taskCategoryId'],
            }
            filterResult[0].childpage.push(temp);
            filterResult[0].countNum = filterResult[0].countNum + element['countNum']
          } else {
            temp = {
              countNum: element['countNum'],
              scheduleName: element['scheduleName'],
              scheduleTypeId: element['scheduleTypeId'],
              taskCategoryId: element['taskCategoryId'],
              childpage: [
                {
                  countNum: element['countNum'],
                  createDate: element['createDate'],
                  isNew: element['isNew'],
                  scheduleName: element['scheduleName'],
                  scheduleTypeId: element['scheduleTypeId'],
                  statusId: element['statusId'],
                  subSysId: element['subSysId'],
                  taskCategoryId: element['taskCategoryId'],
                }
              ]
            }
            approvelist.push(temp);
          }
        }
        approvelist.forEach(el => {
          for (let i = 0; i < this.littleEvent.length; i++) {
            if (el.scheduleTypeId == this.littleEvent[i].value) {
              el['titlename'] = this.littleEvent[i].label
              break;
            }
          }
        });
      }
      this.list = _.clone(approvelist);
      setTimeout(() => {
        this.start()
      }, 1500);
      this.disinNav = true;
      this.inNav = this.list[0];
      if (this.partyOrGroupNo) {
        this.inNav['partyOrGroupNo'] = this.partyOrGroupNo;
      }
    })
  }
  navclick(item) {
    this.inNav = item;
    if (this.partyOrGroupNo) {
      this.inNav['partyOrGroupNo'] = this.partyOrGroupNo;
    }
  }
  start() {
    $(document).ready(function () {
      $('.menu_list ul li').eq(0).children('.div1').show().children('.zcd').eq(0).addClass('removes');
      //     绑定元素点击事件
      $(".menu_list ul li").click(function () {
        //判断对象是显示还是隐藏
        if ($(this).children(".div1").is(":hidden")) {
          //表示隐藏
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(180deg)' });
            //如果当前没有进行动画，则添加新动画
            $(this).children(".div1").animate({
              height: 'show'
            }, 200)
              //siblings遍历div1的元素
              .end().siblings().find(".div1").hide(200);
            $(this).siblings().children(".xiala").css({ 'transform': 'rotate(360deg)' });
          }
        } else {
          //表示显示
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(360deg)' });
            $(this).children(".div1").animate({
              height: 'hide'
            }, 200)
              .end().siblings().find(".div1").hide(200);
          }
        }
      });

      //阻止事件冒泡，子元素不再继承父元素的点击事件
      $('.div1').click(function (e) {
        e.stopPropagation();
      });

      //点击子菜单为子菜单添加样式，并移除所有其他子菜单样式
      $(".menu_list ul li .div1 .zcd").click(function () {
        //设置当前菜单为选中状态的样式，并移除同类同级别的其他元素的样式
        $(this).addClass("removes").siblings().removeClass("removes");
        //遍历获取所有父菜单元素
        $(".div1").each(function () {
          //判断当前的父菜单是否是隐藏状态
          if ($(this).is(":hidden")) {
            //如果是隐藏状态则移除其样式
            $(this).children(".zcd").removeClass("removes");
          }
        });
      });
    });
  }
}
