export class taskpoolPageBean {
    itemType: string;
    bigClassName: string; //事项类型
    littleClassName: string; //事项名称
    userId: string; //任务处理用户id
    noStartFlag: string; //是否包含未开始任务
    endTime: string; // 任务截止日期
    pageRows: number; // 每页记录数 //pageSize
    curPage: number; // 当前页数 // pageNum
    startRows: number; // 开始记录数
    totalRows: number; // 总记录数
    itemName: string;
    endFlag: string;
    nodeType: string; // 0：审批 1：工作任务
    taskPoolFlag: string;// 任务池标记 1-是 0-非
}

