import { Component, OnInit, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';//事项类型的接口
import { ActivatedRoute, Router } from '@angular/router';
import { BtnPageBean } from '../bean/btnPage.bean';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { TZB_HTTP_XIN, TZB_HTTP_XIN_C } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ApprovePageBean } from './bean/approvePage.bean';
/**
 * 审批事项
 */
@Component({
  selector: 'app-one-approve-page',
  templateUrl: './one-approve-page.component.html',
  styleUrls: ['./one-approve-page.component.scss'],
  providers: [CusOperationHttpService, CustomeHttpService, Commfunc]
})
export class OneApprovePageComponent implements OnInit, OnChanges {
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  littleEvent: any = [];
  scheduleTypeParentId: string;
  btnPageBean: BtnPageBean = new BtnPageBean();
  itemsSmallType: any = [
  ];
  list: any = [];//接收后台数据
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: number;
  //每页多少条
  pageSize: number;
  pageNum: number;
  userId: string = '';
  display: boolean = false;
  showModel: string;
  inValue: any;
  first: any;//分页控制
  //获取所有码值
  code: any;
  param: any;
  flag: string;
  HTTPIPDress: any;
  approvePageBean: ApprovePageBean = new ApprovePageBean();
  tableList: any = [];
  user: any;
  general:boolean = false;
  constructor(
    public router: Router,
    public routeInfo: ActivatedRoute,
    public cusOperationHttpService: CusOperationHttpService,
    public customeHttpService: CustomeHttpService,
    public activatedRouter: ActivatedRoute,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.littleEvent = this.code['ScheduleType'];
    this.itemsSmallType = this.code['ItemType'];
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.userId = this.user.userId;
  }

  ngOnInit() {
     //判断是否是总行或者集团
     let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
     let a = commonHeader.extParam.operatingLevel;
     if(a=='GROUP' || a=='GENERAL_BANK' || a=='GENERAL_BANK_DT'){
       this.general=true;
     }else{
       this.general=false;
     }
 
     this.activatedRouter.queryParams.subscribe(params => {
       this.flag = params.flag;
 
     })
    // this.queryCopApprTaskSum();
    // this.querySecle();
    this.approvePageBean.pageRows = 10000000;
    this.approvePageBean.curPage = 1;
    this.first = 0;
    if(this.flag == '2'){
      this.queryCopApprTaskSum()
    }
  }
  ngOnChanges() {
    this.ngOnInit()
  }

  //客户运营-审批事项
  queryCopApprTaskSum() {
    let param = {};
    if (this.flag == '2') {
      param = { statusId: '1' }
    }
    let ob = this.customeHttpService.queryCopApprTaskSum(param);
    ob.subscribe(data => {
        this.list = data.resultList
    })
  }

  // 初始页码查询
  querySecle() {
    
    this.queryCopApprTaskSum();
  }



  //点击审批按钮
  toDisposeSchedule(item) {
    if (item.url && item.url != '') {
      //信贷工作任务处理
      let timestamp = new Date().getTime();
      let target = "custLinkForm" + timestamp;
      // this.commfunc.createBlank(target);

      //审批事项中的信贷
      let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      let userId = user.userId;
      let targetId = user.targetId;
      let params = {
        userId: this.userId,
        sysCode: '122',
        casServerLoginUrl: 'http://10.1.39.140:8011/searchPaymentTool',
        serverName: 'ywsn',
        validateType: '0',
        extraInfo: 'ywsn',
        encryptType: 'HS256',
      }
      this.customeHttpService.tokenApply(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          let token = data.tokenCode;
          if (targetId == '101010') {
            this.HTTPIPDress = TZB_HTTP_XIN;
          } else if (targetId == '101011') {
            this.HTTPIPDress = TZB_HTTP_XIN_C;
          }
          let url = this.HTTPIPDress + 'plats/FrontController?urlParam=' + item.url + '&suid=' + userId + '&token=' + token;
          console.log(url)
          let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
          let closeOnLoseFocus = 'closeOnLoseFocus';
          this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
      })

    } else {
      if (item.systemCode) {
        this.commfunc.setSessionDataCH('systemCode', JSON.stringify(item.systemCode));
      }

      if(this.flag=='1'||!this.flag){
        this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/one-approved-items', { scheduleTypeId: item.scheduleTypeId, scheduleName: item.scheduleName }], { queryParams: { flag: this.flag, 'id': '2' } });
      }
      if(this.flag=='2'){
        this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-pages/one-approved-items', { scheduleTypeId: item.scheduleTypeId, scheduleName: item.scheduleName }], { queryParams: { flag: this.flag, 'id': '2' } });
      }

    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
