import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';//事项类型的接口
import { ActivatedRoute, Router } from '@angular/router';
import { BtnPageBean } from '../bean/btnPage.bean';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_HTTP_XIN, TZB_HTTP_XIN_C } from 'app/@uisftech/common/service/http.constant';
import * as $ from 'jquery';

/**
 * 工作任务
 */
@Component({
  selector: 'app-workdesk-page',
  templateUrl: './workdesk-page.component.html',
  styleUrls: ['./workdesk-page.component.scss',
    './../menu-tree-ul.component.scss'
  ],
  providers: [CusOperationHttpService, CustomeHttpService, Commfunc]
})
export class WorkdeskPageComponent implements OnInit, OnChanges {
  disinNav: boolean = false;
  inNav: any;
  ulData: any = [];
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  littleEvent: any = [];
  scheduleTypeParentId: string;
  btnPageBean: BtnPageBean = new BtnPageBean();
  itemsSmallType: any = [
  ];
  list: any = [];//接收后台数据
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: number;
  //每页多少条
  pageSize: number;
  pageNum: number;
  userId: string = '';
  display: boolean = false;
  showModel: string;
  inValue: any;
  first: any;//分页控制
  HTTPIPDress: any;
  //获取所有码值
  code: any;
  flag: string;
  constructor(
    public router: Router,
    public routeInfo: ActivatedRoute,
    public cusOperationHttpService: CusOperationHttpService,
    public customeHttpService: CustomeHttpService,
    public activatedRouter: ActivatedRoute,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.littleEvent = this.code['ScheduleType'];
  }
  bussinessWhere: string;
  startUserId: any;
  ngOnInit() {
    this.activatedRouter.queryParams.subscribe(params => {
      this.flag = params.flag;
    })
    let session = window.sessionStorage;
    if (session.getItem('bussinessWhere')) {
      let bussinessWhere = session.getItem('bussinessWhere');
      if (bussinessWhere.indexOf('startUserId') != -1) {
        let t = bussinessWhere.split('=');
        this.startUserId = t[1];
      } else {
        this.bussinessWhere = bussinessWhere;
      }
    }
    this.queryCopWorkTaskSum();
    // this.loanScheduleCountByTypeQry()
    if (this.commfunc.getSessionDataCH('markFlag')) {
      this.commfunc.clearSession('markFlag');
    }
    if (this.commfunc.getSessionDataCH('taskId')) {
      this.commfunc.clearSession('taskId');
    }
    if (this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')) {
      this.commfunc.clearSession('bpmQueryTaskOnlyList');
    }

  }
  ngOnChanges() {
    this.ngOnInit()
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //客户运营-工作任务
  queryCopWorkTaskSum() {
    let param = {};
    if (this.flag == '2') {
      param = { statusId: '1' }
    }
    let ob = this.customeHttpService.queryCopWorkTaskSum(param);
    ob.subscribe(data => {
      if (this.flag == '2') {
        this.list = data.resultList;
      } else {
        if (data.resultList && data.resultList.length > 0) {
          this.loanScheduleCountByTypeQry(data.resultList)
        } else {
          this.loanScheduleCountByTypeQry([])
        }
      }
    })
  }
  //信贷-工作任务
  loanScheduleCountByTypeQry(datas) {
    let param = {};
    if (this.flag == '2') {
      param = { statusId: '1' }
    }
    let ob = this.customeHttpService.loanScheduleCountByTypeQry(param);
    ob.subscribe(data => {
      let arr = []
      if (data.resultList && data.resultList.length > 0) {
        let array = data.resultList.concat(datas)
        arr.push(...array);
      } else {
        arr.push(...datas)
      }
      this.bpmQueryUnDoTaskCountGroup(arr)
    })
  }
  nodata: boolean = false;
  maskLayer: boolean = true;
  // 查询待办列表（汇总）
  bpmQueryUnDoTaskCountGroup(datse) {
    this.nodata = false;
    this.maskLayer = true;
    let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let userId = user.userId;
    let bussinessWhere;
    let startUserId;
    if (this.bussinessWhere) {
      bussinessWhere = this.bussinessWhere
    }
    if (this.startUserId) {
      startUserId = this.startUserId
    }
    let params = {
      taskPoolFlag: '3',
      itemType: 'KHLX',
      itemCtrl: '1',
      pageRows: 10000000,
      curPage: 1,
      endFlag: '0',
      userId: userId,
      nodeType: '1',
      bussinessWhere,
      startUserId,
    };
    this.customeHttpService.bpmQueryUnDoTaskCountGroup(params).subscribe(data => {
      if (data.returnCode.code == "success") {
        let arre = [];
        if (data.pageData.result && data.pageData.result.length > 0 || datse.length != 0) {
          if (data.pageData.result && data.pageData.result.length > 0) {
            let arres = [];
            data.pageData.result.forEach(itemn => {
              arres.push({ timeOutCount: itemn.timeOutCount, scheduleTypeId: itemn.bigClassNo, littleClassNo: itemn.littleClassNo, scheduleName: itemn.littleClassName, countNum: itemn.sysCount, bigClassName: itemn.bigClassName, bigClassNo: itemn.bigClassNo, systemCode: itemn.systemCode, workFlag: 'new' });
            });
            this.workrove(datse, arres)
          } else {
            this.workrove(datse)
          }
        } else {
          this.nodata = true;
          $('#appR').height(700);
          this.maskLayer = false;
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }

  workrove(datse, worklist?) {
    let loandate = []; let loandate1 = null; let copdate = []; let copdate1 = null;
    if (datse && datse.length > 0) {
      loandate1 = { bigClassName: '信贷系统', countNum: 0 * 1, childpage: [] }
      copdate1 = { bigClassName: '客运任务', countNum: 0 * 1, childpage: [] }
      datse.forEach(e => {
        if (e.subSysId && e.subSysId == "COP") {
          copdate1.countNum = copdate1.countNum * 1 + e.countNum * 1;
          copdate1.childpage.push(e)
        } else {
          loandate1.countNum = loandate1.countNum * 1 + e.countNum * 1;
          loandate1.childpage.push(e)
        }
      });
    };
    loandate.push(loandate1);
    copdate.push(copdate1);
    loandate = copdate.concat(loandate);
    console.log(loandate, '2222')
    let workrovelist = [];
    let arr = [];
    if (worklist) {
      for (let i = 0; i < worklist.length; i++) {
        let element = worklist[i];
        let filterResult = workrovelist.filter(x => x.bigClassNo == element['bigClassNo']);
        let exist = filterResult && filterResult.length;
        let temp = null;
        if (exist) {
          temp = {
            scheduleName: element['scheduleName'],
            littleClassNo: element['littleClassNo'],
            countNum: element['countNum'],
            timeOutCount: element['timeOutCount'],//超时
            urgentCount: element['urgentCount'],//紧急
            systemCode: element['systemCode'],
            workFlag: element['workFlag']
          }
          filterResult[0].childpage.push(temp);
          filterResult[0].countNum = filterResult[0].countNum + element['countNum'];
          filterResult[0].bigtimeOutCount = filterResult[0].bigtimeOutCount + element['timeOutCount'];
          filterResult[0].bigurgentCount = filterResult[0].bigurgentCount + element['urgentCount']
        } else {
          temp = {
            bigClassName: element['bigClassName'],
            bigClassNo: element['bigClassNo'],
            countNum: element['countNum'],
            bigtimeOutCount: element['timeOutCount'],//大类超时总数
            bigurgentCount: element['urgentCount'],//大类紧急总数
            childpage: [
              {
                scheduleName: element['scheduleName'],
                littleClassNo: element['littleClassNo'],
                countNum: element['countNum'],
                timeOutCount: element['timeOutCount'],//超时
                urgentCount: element['urgentCount'],//紧急
                systemCode: element['systemCode'],
                workFlag: element['workFlag']
              }
            ]
          }
          workrovelist.push(temp);
        }
      }
    }
    // for (let t = 0; t < workrovelist.length; t++) {
    //   const e = workrovelist[t];
    //   if (e.bigClassNo == 'SXSX') {
    //     let pag = {
    //       scheduleName: '所有授信事项',
    //       littleClassNo: e['bigClassNo'],
    //       countNum: e['countNum'],
    //       timeOutCount: e['bigtimeOutCount'],
    //       urgentCount: e['bigurgentCount'],
    //       systemCode: 'SXSX:授信事项;SXSX:授信事项',
    //       biaoji: '手工加全部',
    //     }
    //     e.childpage.unshift(pag)
    //     break;
    //   }
    // }
    let arrays = workrovelist.concat(loandate);
    console.log(arrays, 'ff')
    arr.push(...arrays);
    this.ulData = arr;
    let session = window.sessionStorage;
    if (session.getItem('workliuCode')) {
      let f = session.getItem('workliuCode');
      let d = f.split('&');
      if (!arr[d[0]]) {
        d[0] = '0'; d[1] = '0';
      } else if (!arr[d[1]]) {
        d[1] = '0'
      }
      let a = d[0] + '&' + d[1];
      session.setItem('workliuCode', a);
      this.inNav = arr[d[0]].childpage[d[1]];
    } else {
      this.inNav = arr[0].childpage[0];
    }
    setTimeout(() => {
      this.start();
      this.maskLayer = false;
    }, 1500);
    this.disinNav = true;
  }
  navclick(item) {
    if (item.littleClassNo) {
      if (this.commfunc.getSessionDataCH('workliuCode')) {
        this.commfunc.clearSession('workliuCode');
        this.commfunc.clearSession('workliuData');
      }
      for (let t = 0; t < this.ulData.length; t++) {
        for (let j = 0; j < this.ulData[t].childpage.length; j++) {
          if (this.ulData[t].childpage[j].littleClassNo == item.littleClassNo) {
            let session = window.sessionStorage;
            let a = t + '&' + j;
            let b = JSON.stringify(item);
            session.setItem('workliuCode', a);
            session.setItem('workliuData', b);
            break;
          }
        }
      }
    }
    if (item.url && item.url != '') {
      if (this.commfunc.getSessionDataCH('workliuCode')) {
        this.commfunc.clearSession('workliuCode');
        this.commfunc.clearSession('workliuData');
      }
      //信贷工作任务处理
      let timestamp = new Date().getTime();
      let target = "custLinkForm" + timestamp;
      // this.commfunc.createBlank(target);

      //审批事项中的信贷
      let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      let userId = user.userId;
      let targetId = user.targetId;
      let params = {
        userId: this.userId,
        sysCode: '122',
        casServerLoginUrl: 'http://10.1.39.140:8011/searchPaymentTool',
        serverName: 'ywsn',
        validateType: '0',
        extraInfo: 'ywsn',
        encryptType: 'HS256',
      }
      this.customeHttpService.tokenApply(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          let token = data.tokenCode;
          if (targetId == '101010') {
            this.HTTPIPDress = TZB_HTTP_XIN;
          } else if (targetId == '101011') {
            this.HTTPIPDress = TZB_HTTP_XIN_C;
          }
          let url = this.HTTPIPDress + 'plats/FrontController?urlParam=' + item.url + '&suid=' + userId + '&token=' + token;
          let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
          let closeOnLoseFocus = 'closeOnLoseFocus';
          this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
      })

    }
    // else if (item.littleClassNo == 'DXLC' || item.littleClassNo == 'XYKZX_DXLC') {    //电销  XYKZX_DXLC(信用卡直销_电销岗)
    //   if (this.flag == '1' || !this.flag) {//待办
    //     let param = { bigClassNo: item.bigClassNo, littleClassName: item.scheduleName, littleClassNo: item.littleClassNo, Historyflag: 'isWait' }
    //     this.commfunc.setSessionDataCH('bpmQueryTaskOnlyList', JSON.stringify(param));
    //     this.router.navigate(['/pages/tzb/custom/teleMarketing/outer-rask'], { queryParams: { flag: this.flag, 'id': '3' } });
    //   }
    // } 
    else {
      if (item.systemCode) {
        this.commfunc.setSessionDataCH('systemCode', JSON.stringify(item.systemCode));
      }
      this.inNav = _.clone(item);
    }
  }

  start() {
    $(document).ready(function () {
      let session = window.sessionStorage;
      if (session.getItem('bussinessWhere')) {
        $('.menu_list ul li').eq(0).children('.div1').show().children('.zcd').eq(0).addClass('removes');
      } else if (session.getItem('workliuCode')) {
        let f = session.getItem('workliuCode');
        let d = f.split('&');
        $('.menu_list ul li').eq(d[0]).children('.div1').show().children('.zcd').eq(d[1]).addClass('removes')
      } else {
        $('.menu_list ul li').eq(0).children('.div1').show().children('.zcd').eq(0).addClass('removes');
      }
      //     //绑定元素点击事件
      $(".menu_list ul li").click(function () {
        //判断对象是显示还是隐藏
        if ($(this).children(".div1").is(":hidden")) {
          //表示隐藏
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(180deg)' });
            //如果当前没有进行动画，则添加新动画
            $(this).children(".div1").animate({
              height: 'show'
            }, 200)
              //siblings遍历div1的元素
              .end().siblings().find(".div1").hide(200);
            $(this).siblings().children(".xiala").css({ 'transform': 'rotate(360deg)' });
          }
        } else {
          //表示显示
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(360deg)' });
            $(this).children(".div1").animate({
              height: 'hide'
            }, 200)
              .end().siblings().find(".div1").hide(200);
          }
        }
      });

      //阻止事件冒泡，子元素不再继承父元素的点击事件
      $('.div1').click(function (e) {
        e.stopPropagation();
      });

      //点击子菜单为子菜单添加样式，并移除所有其他子菜单样式
      $(".menu_list ul li .div1 .zcd").click(function () {
        //设置当前菜单为选中状态的样式，并移除同类同级别的其他元素的样式
        $(this).addClass("removes").siblings().removeClass("removes");
        //遍历获取所有父菜单元素
        $(".div1").each(function () {
          //判断当前的父菜单是否是隐藏状态
          if ($(this).is(":hidden")) {
            //如果是隐藏状态则移除其样式
            $(this).children(".zcd").removeClass("removes");
          }
        });
      });
    });
  }
}
