import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BtnPageComponent } from './btn-page.component';
import { ApprovePageComponent } from './approve-page/approve-page.component';
import { WorkdeskPageComponent } from './workdesk-page/workdesk-page.component';
import { TipPageComponent } from './tip-page/tip-page.component';
import { RemindersComponent } from './tip-page/reminders/reminders.component';
import { WorktaskComponent } from './workdesk-page/worktask/worktask.component';
import { ApprovedItemsComponent } from './approve-page/approved-items/approved-items.component';
import { OneApprovePageComponent } from './oneHistory/one-approve-page.component';
import { OneApprovedItemsComponent } from './oneHistory/one-approved-items/one-approved-items.component';
import { TaskPoolComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/task-pool/task-pool.component';
import { TaskPoolItemsComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/task-pool/task-pool-items/task-pool-items.component';
import { HandlingMmattersItemsComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/handling-matters/handling-matters-items/handling-matters-items.component';
import { HandlingMattersComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/handling-matters/handling-matters.component';
import { CoustomerContactComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/customer-contact/coustomer-contact.component';
import { WorkNoneComponent } from 'app/pages/tzb/custom/reminder-do/backlog-detail/btn-page/work-none/work-none.component';

//页面

const routes: Routes = [
    {
        path: '',
        component: BtnPageComponent,
        children: [
            { path: '', redirectTo: 'approve-page', pathMatch: 'full' },
            { path: 'approve-page', component: ApprovePageComponent },
            { path: 'workdesk-page', component: WorkdeskPageComponent },
            { path: 'tip-page', component: TipPageComponent },
            { path: 'reminders', component: RemindersComponent },
            { path: 'worktask', component: WorktaskComponent },
            { path: 'approved-items', component: ApprovedItemsComponent },
            { path: 'one-approved-items', component: OneApprovedItemsComponent },
            { path: 'one-approve-page', component: OneApprovePageComponent },
            { path: 'task-pool', component: TaskPoolComponent },
            { path: 'task-pool-items', component: TaskPoolItemsComponent },
            { path: 'handling-matters', component: HandlingMattersComponent },
            // { path: 'handling-matters-items', component: HandlingMmattersItemsComponent },
            { path: 'coustomer-contact', component: CoustomerContactComponent },
            { path: 'work-none', component: WorkNoneComponent },

            { path: 'approve-page-search', component: ApprovePageComponent },
            { path: 'approve-page-search-search', component: ApprovePageComponent },
            { path: 'workdesk-page-search', component: WorkdeskPageComponent },
            { path: 'workdesk-page-search-search', component: WorkdeskPageComponent },
            { path: 'tip-page-search-search', component: TipPageComponent },
            { path: 'tip-page-search', component: TipPageComponent },
            { path: 'coustomer-contact-search-search', component: CoustomerContactComponent },
            { path: 'coustomer-contact-search', component: CoustomerContactComponent },
            { path: 'work-none-search-search', component: WorkNoneComponent },
            { path: 'work-none-search', component: WorkNoneComponent },
            { path: 'handling-matters-search-search', component: HandlingMattersComponent },
            { path: 'handling-matters-search', component: HandlingMattersComponent },
            { path: 'task-pool-search-search', component: TaskPoolComponent },
            { path: 'task-pool-search', component: TaskPoolComponent },
        ]
    }
];
export const BtnPageRouting: ModuleWithProviders = RouterModule.forChild(routes);