// c查询类
export class QueryUnprocessedCommunicationEventBean {
    custId: string; //	客户号
    custName: string; //	客户名称
    marketingContactWay: any = []; //	联系方式
    marketingContactTheme: any = []; //	联系主题
    fromDateStart: string; //	计划联系时间-起
    fromDateEnd: string; //	计划联系时间-止
    fromDate: string; //	计划联系时间-止
    remark: string; //	计划联系时间-止
    statusId: any = []; //	处理状态
    priority: string; //	优先级
    contactSource: any = []; //	计划来源
    pageNum: number = 1; //	页码
    pageSize: number = 10; //	页数
    lastTimeContact: string; // 上次联系时间
    partyIdFrom:string; //创建人id
    partyNameFrom:string; // 创建人姓名
}

export class CustomTenTypeBean{
    userId:string;//用户id
    sysCode:string;//系统id
    casServerLoginUrl:string;//CAS服务器url
    serverName:string;//申请系统的域名
    validateType:string;//Token的验证类型
    extraInfo:string;//拓展信息
    encryptType:string;//加密方式
}