import { Component, OnInit, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';//事项类型的接口
import { ActivatedRoute, Router } from '@angular/router';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { ContactInformation, ProcessingStarts, LinkageTheme, TourcesPlanning, TaskStatusQueryTwo } from '../../../../custom-contact-new/custom-constant/custom-code-value';
/**
 * 提醒事项
 */
@Component({
  selector: 'coustomer-contact',
  templateUrl: './coustomer-contact.component.html',
  styleUrls: ['./coustomer-contact.component.scss'],
  providers: [CusOperationHttpService, CustomeHttpService, Commfunc]
})
export class CoustomerContactComponent implements OnInit, OnChanges {
  first: number = 1;
  curPage: number;
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage;
  littleEvent: any = [];
  scheduleTypeParentId: string;
  itemsSmallType: any = [];
  inValueDetaile: any;
  list: any = [];//接收后台数据
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: number;
  //每页多少条
  pageSize: number;
  pageNum: number;
  pageRows: number = 10;
  userId: string = '';
  display: boolean = false;
  showModel: string;
  inValue: any;
  detailList: any;
  //获取所有码值
  code: any;
  flag: string;
  queryList: any = [
    {
      label: '客户号',
      key: 'query_indexc',
      value: ''
    }, {
      label: '联系主题',
      key: 'query_indexb',
      value: ''
    }, {
      label: '计划来源',
      key: 'query_indexd',
      value: ''
    },]

  contactInformation: any = ContactInformation; //联系方式
  processingStarts: any = TaskStatusQueryTwo; //处理状态
  linkageTheme: any = LinkageTheme; // 联系主题
  tourcesPlanning: any = TourcesPlanning; // 计划来源

  constructor(
    public router: Router,
    public cusOperationHttpService: CusOperationHttpService,
    public customeHttpService: CustomeHttpService,
    public activatedRouter: ActivatedRoute,
    public commfunc: Commfunc,
    public routeInfo: ActivatedRoute,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.littleEvent = this.code['ScheduleType'];
  }
  bussinessWhereDATA: any;
  bussinessWhere: any;
  ngOnInit() {
    this.activatedRouter.queryParams.subscribe(params => {
      console.log(params)
      this.flag = params.flag;
    })

    //客户联系处理成功提示
    this.detailList = this.routeInfo.snapshot.params;
    console.log(this.detailList);
    // if (this.detailList && this.detailList.code) {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'success', summary: '提示', detail: "处理成功" });
    // }
    this.queryTask();
  }

  ngOnChanges() {
    this.ngOnInit()
  }

  //点击处理按钮
  toDisposeSchedule(item) {
    // 告诉工作流该实例被查看
    let parm = {
      taskId: item.taskId,
      userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId
    }
    let params = {
      isFront: 0,
      communicationEventId:item.communicationEventId
    }
    this.cusOperationHttpService.updateContactWorkEffortStatus(params).subscribe(data=>{
      if (data.returnCode.code == 'success') {
        this.customeHttpService.bpmViewTaskInfo(parm).subscribe(data => { })
        this.router.navigate(['/pages/tzb/custom/custom-contact-new/custom-contact-list-handle', { varMap: JSON.stringify({ dataMap: item }), flag: 1 }]);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  // 详情按钮
  details(item) {
    this.display = true;
    this.inValueDetaile = item
  }

  //客户联系
  queryTask() {
    let par = '';
    for (let item of this.queryList) {
      if (item.key && item.value) {
        if (!par) {
          par = `${item.key}=${"'" + item.value + "'"}`
        } else {
          par = par + ` and ${item.key}=${"'" + item.value + "'"}`
        }
      }
    }
    let session = window.sessionStorage;
    if (session.getItem('bussinessWhere')) {
      this.bussinessWhereDATA = session.getItem('bussinessWhere');
      let a = this.bussinessWhereDATA + ' and ' + par;
      if (par != '') {
        this.bussinessWhere = a;
      } else {
        this.bussinessWhere = this.bussinessWhereDATA;
      }
    } else {
      this.bussinessWhere = par;
    }
    let queryOrder;// by zj
    if (this.queryOrder) {
      queryOrder = this.queryOrder
    }
    let param = {
      // systemCode: "0308:客户联系;0308_01:客户联系_工作任务",
      nodeType: "1",
      pageRows: this.pageRows,
      curPage: this.curPage,
      itemType: 'KHLX',
      itemNode: '0308_01',
      itemCtrl: '0',//0-匹配 1-排除
      userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
      bussinessWhere: this.bussinessWhere,
      queryOrder,
    };
    this.customeHttpService.bpmQueryUnDoTaskList(param).subscribe(data => {
      this.total = data.pageData.totalRows
      this.list = [];
      data.pageData.result.forEach(element => {
        element.varMap.dataMap['taskId'] = element.taskId;// by zj 添加taskid
        element.varMap.dataMap['createDate'] = element.createDate.slice(0, 16);// by zj
        element.varMap.dataMap['fromDate'] = element.varMap.dataMap['fromDate'].slice(0, 16);// by zjl
        element.varMap.dataMap['viewFlag'] = element.viewFlag;// by zjl  添加处理状态
        // element.varMap.dataMap['isTimeout'] = element.isTimeout;// by zjl  添加超时时间
        this.list.push(element.varMap.dataMap);
      });
      console.log(this.list)
    })
  }
  //跳转客户视图
  tocust(car) {
    this.commfunc.setSessionData('custNo', car.obCustomercode); //客户号的取值 by zj
    window.open('#/pages/custom-view/general', '_blank')
  }
  // 分页
  paginate(event) {
    this.pageRows = event.rows * 1;
    let currentPage = event.page + 1;
    this.curPage = currentPage;
    this.first = (currentPage - 1) * this.pageRows;
    this.queryTask()
  }
  querySecle() {

  }
  sortdis1: boolean = true;
  sortdis2: boolean = true;
  sortdis3: boolean = true;
  asc: boolean = false;
  desc: boolean = false;
  queryOrder: any;
  // 升降序
  sorttable(typ, key?) { // by zj
    if (key && key == '1') {//升
      this.asc = false;
      this.desc = true;
      this.sortfuntion(typ, '1');
    } else if (key && key == '2') { //降
      this.asc = true;
      this.desc = false;
      this.sortfuntion(typ, '2');
    } else {
      if (typ == 'cust') {
        this.sortdis1 = false;
        this.sortdis2 = true;
        this.sortdis3 = true;
      }
      else if (typ == 'amt') {
        this.sortdis1 = true;
        this.sortdis2 = false;
        this.sortdis3 = true;
      }
      else if (typ == 'time') {
        this.sortdis1 = true;
        this.sortdis2 = true;
        this.sortdis3 = false;
      }
      this.asc = true;//默认升序
      this.sorttable(typ, '1')
    }
  }

  sortfuntion(typ, key) { // by zj
    if (typ == 'time') {
      if (key == '1') {
        this.queryOrder = 'create_date asc'//从小到大
      }
      if (key == '2') {
        this.queryOrder = 'create_date desc'//从大到小
      }
      this.queryTask();
    } else if (typ == 'cust') {
      if (key == '1') {
        this.queryOrder = 'query_Indexc asc'//从小到大
      }
      if (key == '2') {
        this.queryOrder = 'query_Indexc desc'//从大到小
      }
      this.queryTask();
    }
  }

  reset() {
    this.queryList = [
      {
        label: '客户号',
        key: 'query_indexc',
        value: ''
      }, {
        label: '联系主题',
        key: 'query_indexb',
        value: ''
      }, {
        label: '计划来源',
        key: 'query_indexd',
        value: ''
      },]
    this.pageRows = 10;
    this.pageNum = 1;
    this.first = 0;
    this.queryTask()
  }

  detailsReturn(event) {
    this.display = event.display
  }
  peopleDisplay: boolean = false;
  // 分配
  tatchClick() {
    this.peopleDisplay = true;
  }
  choicesaleUser(evet) {

  }
}
