import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { Message } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute } from '@angular/router';
import { CusOperationHttpService } from '../../../../../http/custom-operation/custom-operation.http.service';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ProcessInstanceManagementService } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance-management/process-instance-management.service';
import { HttpService } from 'app/pages/tzb/workflow/activiti-work-flow/common/service/http.service';
import { MessageService } from 'primeng/components/common/messageservice';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';
import * as _ from 'lodash';
import { ContactInformation, LinkageTheme, TourcesPlanning, PushType2 } from "app/pages/tzb/custom/custom-contact-new/custom-constant/custom-code-value";

@Component({
	selector: 'work-none-items',
	templateUrl: './work-none-items.component.html',
	styleUrls: ['./work-none-items.component.scss'],
	providers: [CusOperationHttpService, Commfunc, CustomeHttpService, ConfirmationService, CreditCcmService, ProcessInstanceManagementService, HttpService, MessageService]
})

export class WorkNoneItemsComponent implements OnInit, OnChanges {
	num: any;
	flag: any;
	bussinessWhere: string;
	startUserId: any;
	pageRows: number;
	curPage: number;
	@Input() inNav: any;
	workCreateName: string;
	tabMesg: any = tableMessage;
	scheduleTypeId3: any = [];
	ch: any = CALENDAR_CN;
	userId: string;
	itemsSmallType: any = [];
	msgs: Message[] = [];
	code: any;
	first: number = 0;
	total: number;
	tableList: any = [];
	approveParams: any = [];
	user: any;
	whFlag: string;
	tableTitleList: any = [];
	queryList: any = [];
	paramNUM: string;
	stopFlag: boolean = true;
	processId: string;
	creditApproveDisplay: boolean = false;
	creditApproveDisplay1: boolean = false;
	creditApproveDisplay2: boolean = false;
	nowTaskId: string;
	startDate: any;
	endDate: any;
	contactInformation: any = ContactInformation; //联系方式
	linkageTheme: any = LinkageTheme; //联系主题
	tourcesPlanning: any = TourcesPlanning; //计划来源
	PushType2: any = PushType2;	// 推送类型
	constructor(
		public commonHttpService: CommonHttpService,
		public customeHttpService: CustomeHttpService,
		public httpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public router: Router,
		public activatedRouter: ActivatedRoute,
		public confirmationService: ConfirmationService,
		private creditCcmService: CreditCcmService,
		private processInstanceManagementService: ProcessInstanceManagementService,
	) {
		this.codeValues();
		this.scheduleTypeId3 = this.code['ScheduleType'];
		this.itemsSmallType = this.code['ItemType'];
		this.approveParams = this.code['approveParams']
		this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		if (this.user.userId) {
			this.userId = this.user.userId;
		}
		this.activatedRouter.queryParams.subscribe(params => {
			this.flag = params.flag;
			this.num = params.totalNum;

		})
	}

	ngOnInit() {
		this.pageInit();
	}
	ngOnChanges(): void {
		this.pageInit();
	}

	workarr: any;
	pageInit() {
		this.taskId = null;
		this.tasktype = null;
		this.workarr = null;
		let par = {
			limit: "10",
			page: "1",
			paramName: 'work-noneFunctionConfig',
		}
		this.commonHttpService.queryCommonParamService(par).subscribe(data => {
			if (data.returnCode.code == 'success') {
				if (data.returnCommonParamList && data.returnCommonParamList.length > 0) {
					let arr = data.returnCommonParamList[0];
					this.commonHttpService.queryCommonParamItemService(arr).subscribe(data1 => {
						if (data1.returnCode.code == 'success') {
							if (data1.returnCommonParamItemList && data1.returnCommonParamItemList.length > 0) {
								for (let p = 0; p < data1.returnCommonParamItemList.length; p++) {
									const el = data1.returnCommonParamItemList[p];
									if (el.paramItemName == this.inNav['littleClassNo']) {
										this.workarr = el.paramItemValue;
										break;
									}
								}
							}
						}
					})
				}
			}
		})
		if (this.commfunc.getSessionDataCH('saveMSGIdentyfy') && this.commfunc.getSessionDataCH('saveMSGIdentyfy') == 'true') {
			this.commfunc.clearSession('saveMSGIdentyfy')
		}
		this.paramNUM = '1';
		let session = window.sessionStorage;
		if (session.getItem('para')) {
			session.removeItem('para');
		}
		this.querySecle('1');
	}

	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret;
	}
	bussinessWhereDATA: any;
	querySecle(isOnit?) {
		let param = ''
		if (isOnit != '1') {
			for (let item of this.queryList) {
				if (item.key && item.i) {
					// debugger
					if (!param && item.i != 'createDateFrom' && item.i != 'createDateTo') {
						param = `${item.i}=${"'" + item.key + "'"}`
					} else if (param && item.i != 'createDateFrom' && item.i != 'createDateTo') {
						param = param + ` and ${item.i}=${"'" + item.key + "'"}`
					} else if (!param && item.i == 'createDateFrom' && item.key) {
						item.key = this.commfunc.transDateN(item.key)
						param = `create_date>${"'" + item.key + "'"}`
					} else if (!param && item.i == 'createDateTo' && item.key) {
						item.key = this.commfunc.transDateN(item.key)
						param = `create_date<${"'" + item.key + "'"}`
					} else if (param && item.i == 'createDateFrom' && item.key) {
						item.key = this.commfunc.transDateN(item.key)
						param = param + ` and create_date > ${"'" + item.key + "'"}`
					} else if (param && item.i == 'createDateTo' && item.key) {
						item.key = this.commfunc.transDateN(item.key)
						param = param + ` and create_date < ${"'" + item.key + "'"}`
					}
				}
			}
		}
		let session = window.sessionStorage;
		if (session.getItem('bussinessWhere')) {
			this.bussinessWhereDATA = session.getItem('bussinessWhere');
			if (this.bussinessWhereDATA.indexOf('startUserId') != -1) {
				let t = this.bussinessWhereDATA.split('=');
				this.startUserId = t[1];
				if (param != '') {
					this.bussinessWhere = param;
				}
			} else {
				let a = this.bussinessWhereDATA + ' and ' + param;
				if (param != '') {
					this.bussinessWhere = a;
				} else {
					this.bussinessWhere = this.bussinessWhereDATA;
				}
			}
		} else {
			this.bussinessWhere = param;
		}
		if (session.getItem('month')) {
			this.startDate = JSON.parse(session.getItem('month')).startDate;
			this.endDate = JSON.parse(session.getItem('month')).endDate;
			if (this.bussinessWhere != '') {
				this.bussinessWhere = this.bussinessWhere + 'and' + "('" + this.startDate + "' <" + "create_date" + ' and create_date' + "<'" + this.endDate + "')"
			} else {
				this.bussinessWhere = "'" + this.startDate + "' <" + "create_date" + ' and create_date' + "<'" + this.endDate + "'"
			}
		}
		this.pageRows = 10;
		this.curPage = 1;
		this.first = 0;
		this.bpmQueryTaskList()
	}
	// 重置
	reset() {
		for (let item of this.queryList) {
			if (item.key) {
				item.key = ''
			}
		}
		this.bussinessWhere = '';
		this.workCreateName = undefined;
		this.startUserId = undefined;
		this.pageRows = 10;
		this.curPage = 1;
		this.first = 0;
		this.querySecle()
	}
	disstop: boolean = false;
	// 终止按钮
	toStop(item) {
		this.confirmationService.confirm({
			message: '确认要终止?',
			accept: () => {
				let param = {
					userId: this.userId,
					taskId: item.taskId,
					tranType: 'A0',
					doResion: '4',
				}
				this.customeHttpService.bpmUserTaskManage(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.msgs = [];
						this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
						item['disstop'] = true
					} else {
						this.msgs = [];
						this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
					}
				}, error => {
					this.msgs = [];
					this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
				})
			}
		});
	}
	doUp() {
		setTimeout(() => {
			// 	this.pageRows = 10;
			// 	this.curPage = 1;
			// 	this.querySecle();

			let session = window.sessionStorage;
			let url = window.location.href;
			let c;
			if (url.indexOf('?') != -1) {
				let a = url.split('?');
				let h = a[0];
				if (h.indexOf('-search-search') != -1) {
					let k = h.split('');
					let j = k.length - 7;
					let g = h.slice(0, j);
					c = g + '?'
				} else {
					c = h + '-search?';
				}
				let Url = c + a[1];
				let arr = Url.split('#');
				this.router.navigateByUrl(arr[1]);
			}
		}, 300)
	}


	//办结事项任务明细查询
	bpmQueryTaskList() {
		this.itemType = null;
		let nodeType;
		let startUserId;
		let bussinessWhere;
		if (this.num == '3') {
			nodeType = 0
		} else if (this.num == '4') {
			nodeType = 1
		}
		if (this.bussinessWhere) {
			bussinessWhere = this.bussinessWhere
		}
		if (this.startUserId) {
			startUserId = this.startUserId
		}
		let arr = this.inNav.systemCode.split(';');
		let a = arr[0].split(':');
		let b = arr[1].split(':');
		this.itemType = a[0];
		let pram = {
			userId: this.userId,
			isAllNode: false,
			isEndProcess: false,
			itemType: a[0],
			itemNode: b[0],
			first: this.first,
			curPage: this.curPage,
			pageRows: this.pageRows,
			nodeType,
			bussinessWhere,
			startUserId,
		}
		this.customeHttpService.bpmQueryTaskOnlyList(pram).subscribe(data => {
			if (data.returnCode.code == "success") {
				data.pageData.result.forEach(el => {
					el.createDate = el.createDate.slice(0, 16);
					el.updateDate = el.updateDate.slice(0, 16);
				});
				this.tableList = _.clone(data.pageData.result);
				this.total = _.clone(data.pageData.totalRows);
				if (this.paramNUM == '1') {
					let param = '';
					if (data.pageData.result[0].littleClassNo) {
						param = data.pageData.result[0].littleClassNo
					}
					//动态业务参数查询条件
					this.queryTaskNoteMessage('1', param);
					//动态业务参数表头
					this.queryTaskNoteMessage('2', param);
				}
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
		})
	}
	// 关闭审批详情
	closeFirtproc() {
		this.nowTaskId = undefined;
		this.creditApproveDisplay = false;
		this.creditApproveDisplay1 = false;
		this.creditApproveDisplay2 = false;
	}
	// 审批详情弹框回调方法
	creditApproveCall() {
		this.creditApproveDisplay1 = false;
		this.creditApproveDisplay2 = false;
		this.creditApproveDisplay = false;
	}
	// 查看流程进度
	viewProcessSchedule(item): void {
		const processCodeParam = encodeURIComponent(item.processCode);
		const params = `/${item.processCode}/${item.processId}/${item.processState}/userId001/sysCode001/tokenCode001`;
		this.processInstanceManagementService.toActivitiSchedule(params);
	}
	// 查看审批轨迹
	ViewApprovalpath(item) {
		this.processId = item.processId;
		if (item.littleClassNo == 'RUM0001') {
			this.nowTaskId = 'sub';
			this.creditApproveDisplay2 = true;
		} else {
			this.nowTaskId = undefined;
			this.creditApproveDisplay1 = true;
		}
		this.creditApproveDisplay = true;
	}
	// 分页
	paginate(event) {
		this.pageRows = event.rows * 1;
		let currentPage = event.page + 1;
		this.curPage = currentPage;
		this.first = (currentPage - 1) * this.pageRows;
		this.bpmQueryTaskList()
	}
	//追回
	backBtn(item) {
		this.confirmationService.confirm({
			message: '确认要追回?',
			accept: () => {
				let param = {
					userId: this.userId,
					taskId: item.taskId,
					tranType: 'A1',
					doResion: '3',
				}
				if (item.mainFlowNo) {
					this.setSerialNumber(item.mainFlowNo);
				}
				this.customeHttpService.bpmUserTaskManage(param).subscribe(data => {
					if (data.returnCode.code == 'success') {
						this.msgs = [];
						this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
					} else {
						this.msgs = [];
						this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
					}
				}, error => {
					this.msgs = [];
					this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
				})
			}
		});
	}
	taskId: any;
	tasktype: any;
	workerCall(i, item) {
		if (i) { this.tasktype = i };
		this.taskId = item.taskId;
		this.personPage = true;
	}
	musId: any;
	personPage: boolean = false;
	// 现转发
	peopleCall(peoplearr) {
		this.personPage = false;
		let a = [];
		let b = [];
		let c;
		let d;
		let msg;
		for (let i = 0; i < peoplearr.length; i++) {
			a.push(peoplearr[i]['tellerId']) //取出转人ID
			b.push(peoplearr[i]['tellerName'])//取出转人名称
		}
		if (this.tasktype) {
			if (this.tasktype == '2') { d = '转发' } else if (this.tasktype == '3') { d = '转分配' } else if (this.tasktype == '4') { d = '转办' }
		}
		if (b.length > 3) {
			c = b.splice(0, 3).join(',');
			msg = '确认' + d + c + '等' + peoplearr.length + '名人员?'
		} else {
			msg = '确认' + d + b + peoplearr.length + '名人员?'
		}
		this.musId = a.join(',');
		this.confirmationService.confirm({
			message: msg,
			accept: () => {
				if (this.tasktype && this.tasktype == '2') {
					let param = {
						userId: this.userId,
						taskId: this.taskId,
						targetUserId: this.musId,
						taskType: '0',
					}
					this.processInstanceManagementService.bpmadditionTask(param).subscribe(data => {
						if (data.returnCode.code == 'success') {
							this.msgs = [];
							this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
						} else {
							this.msgs = [];
							this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
						}
					}, error => {
						this.msgs = [];
						this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
					})
				} else {
					this.msgs = [];
					this.msgs = [{ severity: 'error', summary: '提示', detail: '服务暂没！！！攻城狮在捉急弄' }];
				}
			},
		})
	}
	setSerialNumber(serialNum) {
		let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		commonHeader['globalSeqNo'] = serialNum;
		this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
	}
	// 详情查看-传入标识markFlag
	toDoExam(data) {
		console.log("ad")
		// 客户联系跳入详情标识
		data['detailFlag'] = 2;// 客户联系进入详情标识： 1： 办结事项进入详情； 2：已办事项
		let worktype = { worktype: data.workType };
		this.commfunc.setSessionDataCH('workType', JSON.stringify(worktype));
		if (this.activatedRouter.snapshot.queryParams['flag'] == '2') {
			data['markFlag'] = '2';
		} else {
			data['markFlag'] = '1';
		}
		if (!(data.varMap.taskCategoryId.indexOf('RUM0005') < 0) ||
			!(data.varMap.taskCategoryId.indexOf('RUM0009') < 0) ||
			!(data.varMap.taskCategoryId.indexOf('RUM_0020') < 0) ||
			!(data.varMap.taskCategoryId.indexOf('XYKZCRGSPLC') < 0) ||
			!(data.varMap.taskCategoryId.indexOf('RUM_0023') < 0) ||
			!(data.varMap.taskCategoryId.indexOf('RUM_0022') < 0)
		) {
			let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
			let post_name = JSON.parse(this.commfunc.getSessionDataCH('postIdList'));
			let products = '';
			if (data.varMap.dataMap.productNo) {
				products = data.varMap.dataMap.productNo;
			} else {
				products = data.varMap.dataMap.productId;
			}
			let params = {};
			if (data.varMap.taskCategoryId == "RUM_0020" || data.varMap.taskCategoryId == "RUM_0026") {
				params = {
					businessType: "17",
					'roleId': post_name,
					'viewIndex': 1,
					'viewSize': 100,
				}
			} else {
				params = {
					'productNo': products,
					'roleId': post_name,
					'viewIndex': 1,
					'viewSize': 100,
				};
			}
			//如果flag为‘2’则是从已办进入的
			let flag = '';
			if (this.activatedRouter.snapshot.queryParams['flag'] == '2') {
				flag = '2';
			} else {
				flag = '';
			}
			this.creditCcmService.queryViewRelations(params).subscribe(datas => {
				if (datas.returnCode.code === 'success') {
					if (datas.list.length > 0) {
						let param, pageParams, littleClassNo;
						if (!(data.varMap.taskCategoryId.indexOf('RUM0009') < 0)) {

							let viewId = [];
							datas.list.forEach(e => {
								if (e.viewName.match('卡要素')) {
									viewId.push({ a: e.viewConfigId, b: e.viewName });
								}
							});
							this.commfunc.setSessionDataCH('viewId', JSON.stringify(viewId));

							for (let i = 0; i < datas.list.length; i++) {
								if (datas.list[i].viewName.match('卡要素')) {
									this.stopFlag = false;
									param = {
										'viewConfigId': datas.list[i]['viewConfigId'],
										'viewName': datas.list[i]['viewName'],
										'flag': true//审批标识
									}
									pageParams = {
										'viewConfigId': datas.list[i]['viewConfigId'],
										'viewName': datas.list[i]['viewName'],
										'custNo': data.varMap.dataMap.custNo,
										'custName': data.varMap.dataMap.custName,
										'custRequestNo': data.varMap.custRequestNo,
										'productNo': products,
										'productName': data.varMap.dataMap.productName,
										'isView': '',
										'businessType': data.varMap.dataMap.businessType,
										'processId': data.processId,
										'taskId': data.taskId,
										'workCreater': data.workCreater,
										'guaranteeType': data.varMap.dataMap.guaranteeType,
										'applyAmt': data.varMap.dataMap.applyAmt,
										'varMap': data.varMap,
										'taskRate': data.taskRate,
										'custRequestId': data.varMap.custRequestId,
										'endDate': data.varMap.dataMap.endDate,
										'taskType': data.taskType,
										'stageType': data.varMap.stageType,
										'taskName': data.taskName,
										'assActorId': data.varMap.dataMap['assActorId'],
										'requestSeqNo': data.varMap.requestSeqNo,
										'flag': flag,
										'processState': data.processState,

									};
									break;
								}
							}
						} else {
							let viewId = [];
							datas.list.forEach(e => {
								if (!e.viewName.match('卡要素')) {
									viewId.push({ a: e.viewConfigId, b: e.viewName });
								}
							});
							this.commfunc.setSessionDataCH('viewId', JSON.stringify(viewId));

							for (let i = 0; i < datas.list.length; i++) {
								let str = '卡要素';
								if (datas.list[i].viewName.indexOf(str) == -1) {
									this.stopFlag = false;
									param = {
										'viewConfigId': datas.list[i]['viewConfigId'],
										'viewName': datas.list[i]['viewName'],
									}
									pageParams = {
										'viewConfigId': datas.list[i]['viewConfigId'],
										'viewName': datas.list[i]['viewName'],
										'custNo': data.varMap.dataMap.custNo,
										'custName': data.varMap.dataMap.custName,
										'custRequestNo': data.varMap.custRequestNo,
										'productNo': products,
										'productName': data.varMap.dataMap.productName,
										'isView': '',
										'businessType': data.varMap.dataMap.businessType,
										'processId': data.processId,
										'taskId': data.taskId,
										'workCreater': data.workCreater,
										'guaranteeType': data.varMap.dataMap.guaranteeType,
										'applyAmt': data.varMap.dataMap.applyAmt,
										'varMap': data.varMap,
										'taskRate': data.taskRate,
										'custRequestId': data.varMap.custRequestId,
										'endDate': data.varMap.dataMap.endDate,
										'stageType': data.varMap.stageType,
										'taskName': data.taskName,
										'taskType': data.taskType,
										'assActorId': data.varMap.dataMap['assActorId'],
										'requestSeqNo': data.varMap.requestSeqNo,
										'flag': flag,
										'processState': data.processState,
										'mainFlowNo': data.mainFlowNo,
										'taskCode': data.taskCode,
										'workType': data.workType
									};
									break;
								}
							}


						}
						if (this.stopFlag) {
							this.msgs = [{ severity: 'info', summary: '错误', detail: '产品和岗位未关联页面,请配置关联页面' }];
						}
						littleClassNo = data.littleClassNo;
						let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
						commonHeader['extParam']['littleClassNo'] = littleClassNo;
						this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
						if (data.varMap.taskCategoryId == "RUM_0023" || data.varMap.taskCategoryId == "RUM_0022") {
							pageParams['custRequestNo'] = data.varMap.dataMap.custRequestNo;
						}
						this.commfunc.setSessionDataCH('creditDataWorkFlow', JSON.stringify(pageParams));
						if (flag == '2') {
							param['ckhs'] = 'Y';
						}
						// RUM_0020批量预授信
						if (data.varMap.taskCategoryId == "RUM_0026" || data.varMap.taskCategoryId == "RUM_0020") {
							// console.log("pp")
							// this.commfunc.setSessionDataCH('approvalBatchBean', JSON.stringify({
							// 	batchId: data.varMap.dataMap.batchId || null,
							// 	groupId: data.varMap.dataMap.groupId || null
							// }));
							// 获取批量预授信客户群信息
							let batchDetail = new Promise(resolve => {
								this.queryBatchDetail(data.varMap.dataMap, resolve)
							})
							let batchGroup = new Promise(resolve => {
								this.queryGroupInfo(data.varMap.dataMap, resolve)
							})
							Promise.all([batchDetail, batchGroup]).then(res => {
								let datas = {
									detailData: res[0]["detailDtas"],
									groupData: res[1]["groupDtas"],
									self: '3',
									selfTwo: '2',
									worktype: data.workType || null
								}
								param['batchIsNo'] = 'Y';
								this.commfunc.setSessionDataCH('groupBranchData', JSON.stringify(datas))
								let batchworktype = '0'
								if (data.taskRate == '1' && (data.workType == '4' || data.workType == '5' || data.workType == '7')) {
									batchworktype = '1'
								}
								// 追回 plRecover
								// 追回
								console.log(pageParams)
								this.commfunc.setSessionDataCH('inputParam', JSON.stringify({ transferCreditData: pageParams }))
								this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify({ plRecover: '1', businessType: "17", notApply: '2', worktype: batchworktype })]);
								// businessType: "17", notApply: notApply,
								// this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify({ businessType: "17", notApply: '2', worktype: batchworktype })]);

								// this.queryViewRelations(item, notApply);
							})

						} else {
							this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify(param)]);
						}




					} else {
						this.msgs = [{ severity: 'info', summary: '错误', detail: '产品和岗位未关联页面,请配置关联页面' }];
					}
				} else {
					this.msgs = [{ severity: 'error', summary: '错误', detail: datas.returnCode.message }];
				}
			});
		}
		// else if(data.varMap.taskCategoryId == 'RUM0011' ) {
		// 	    let param = 
		// 		this.commfunc.setSessionDataCH('overdue-params-from-reminder-do', JSON.stringify(param));
		// 		this.router.navigate(["/pages/tzb/custom/overdue/overdue-case", { from: 'reminder-do' }])
		// }
		else if (!(data.varMap.systemIdCode.indexOf('RUM0011') < 0)) {
			// 参数
			let paramVarMap = {
				createDate: data.varMap.createDate,
				processId: data.varMap.processId,
				taskId: data.taskId,
				bigClassName: data.varMap.bigClassName,//事项类型
				littleClassName: data.varMap.littleClassName,//事项名称
				workCreateName: data.varMap.workCreaterName,
				processCode: data.varMap.processCode,
				markFlag: data['markFlag'],//区分代办和历史
				scheduleType: 'choose',//是否选人
				dataMap: JSON.stringify(data.varMap.dataMap),
				taskName: data.varMap.taskName,
				taskRate: data.varMap.taskRate,
				littleClassNo: data.varMap.littleClassNo,
				mainFlowNo: data.mainFlowNo,
				workType: data.varMap.workType,
			}
			this.commfunc.setSessionDataCH('overdue-params-from-reminder-do', JSON.stringify(paramVarMap));
			//跳转模块化

			// let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
			let littleClassNo = data.littleClassNo;
			let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
			commonHeader['extParam']['littleClassNo'] = littleClassNo;
			commonHeader['globalSeqNo'] = data.mainFlowNo;
			this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
			// let post_name = JSON.parse(this.commfunc.getSessionDataCH('postIdList'));
			// let post_name = JSON.parse(this.commfunc.getSessionDataCH('postIdList'));
			let post_name = [commonHeader['extParam']['postId']]//逾期只传当前岗位ID
			let params = {
				businessType: "08",
				'roleId': post_name,
				'viewIndex': 1,
				'viewSize': 100,
			}
			this.creditCcmService.queryViewRelations(params).subscribe(datas => {
				if (datas.returnCode.code === 'success') {
					if (datas.list.length > 0) {
						let viewId = [];
						datas.list.forEach(e => {
							// if (e.viewName.match('卡要素')) {
							viewId.push({ a: e.viewConfigId, b: e.viewName });
							// }
						});
						this.commfunc.setSessionDataCH('viewId', JSON.stringify(viewId));
						// let param = {

						// }
						// this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify(param)]);
						// /pages/tzb/custom/overdue/overdue-case
						this.router.navigate(["/pages/tzb/custom/overdue/overdue-case", {
							from: 'reminder-do',
							taskId: data.taskId,
							processId: data.processId,
							detail: '1'
						}])
					} else {
						this.msgs = [{ severity: 'info', summary: '错误', detail: '产品和岗位未关联页面,请配置关联页面' }];
					}

				} else {
					this.msgs = [{ severity: 'error', summary: '错误', detail: datas.returnCode.message }];
				}
			})

		} 
		else {
			this.commfunc.toDisposeSchedule(data);
		}
	}

	// 客户群信息查询queryCustGroupInfo
	queryGroupInfo(item, resolve) {
		let params = {
			"permissionFlag": "1",
			"advanceInput": "0",
			"groupType": "0",
			"pageNum": 1,
			"pageSize": 10,
			"conditionList": [
				[
					{
						"field": "group_id",
						"operator": "=",
						"value": item.groupId
					}
				]
			],

		}
		this.httpService.queryCustGroupInfo(params).subscribe(result => {
			if (result.returnCode.code == 'success') {
				let data = result.resultList[0] || {}
				console.log("oop")
				let datas = {
					cust_num: data.open_num || null,//客户总数
					valid_cust_num: data.valid_cust_num || null,//有效客户数
					valid_cov_rate: data.valid_ratio || null,//有效覆盖率
					crdt_cust_num: data.CRDT_CUST_NUM || null,//已授信客户数
					pre_crdt_amt: data.pre_credit_limit || null,//预授信额度总和
					// crdt_cust_num:data.cust_num || null,//已授信客户数
					pre_crdt_cust_num: data.PRE_CRDT_CUST_NUM,// 已经预授信客户数 	
					crdt_amt: data.CRDT_AMT || null,//授信额度总和
					crdt_bal: data.CRDT_BAL || null,//授信余额总和
					loan_cust_num: data.LOAN_CUST_NUM || null,//用信客户数
					// crdt_amt:data.cust_num || null,//已用信额度总和
					CRDT_AMT_USED: data.CRDT_AMT_USED,//已用信额度总和 0
					GROUP_ID: data.group_id || null,//客群号
					GROUP_NAME: data.group_name || null,//客群名称
					GROUP_TYPE: data.group_type || null,//客群类型
					GROUP_OWNER_NAME: data.group_owner || null,//群主
					GROUP_OWNER_ID: data.group_owner_id || null,//经办人
					GROUP_LINKMANNAME: data.group_of_contacts || null,//群联系人
					GROUP_LINKMANID: data.group_of_contacts_id || null,//群联系人ID
					ORG_NAME: data.org_id || null,//所属机构
					ADDRESS: data.address_message || null,//详细地址
					PRODUCT_CODE: data.featured_products || null,//主推产品
					INT: data.INT || null,//备注
					ORG_ID: data.create_org_id || null,//经办机构
					subGroupType: data.subGroupType || null //所属区域
				}
				resolve({
					groupDtas: datas
				})
			} else {
				this.msgs = [{ severity: 'error', summary: '提示', detail: result.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
	//查询详细信息
	queryBatchDetail(item, resolve) {
		let params = {
			batchId: item.batchId
		}
		this.httpService.queryBatchPreCreditInfo(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let datas = {
					batchId: data.batchId,
					operType: data.operType,
					state: data.state,
					groupId: data.groupId,
					groupName: data.groupName,
					productId: data.productId,
					productName: data.productName,
					guaranteeType: data.guaranteeType,
					contractType: data.contractType,
					endDate: data.endDate,
					rateKind: data.rateKind,
					returnKind: data.returnKind,
					note: data.note,
					operId: data.operId,
					operName: data.operName,
					bankId: data.bankId,
					bankName: data.bankName,
					acDate: data.acDate,
					Permissioncontrol: data.permissionControler
				}
				resolve({
					detailDtas: datas
				})

				// this.commfunc.setSessionDataCH('groupBranchData', JSON.stringify(datas))
				// this.queryViewRelations(item);
			} else {
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
	//页面信息查询
	queryViewRelations(products, post_name, resolve) {
		const params = {
			productNo: products,
			roleId: post_name,
			viewIndex: 1,
			viewSize: 100,
		};
		this.creditCcmService.queryViewRelations(params).subscribe(data => {
			if (data.returnCode.code == 'success') {
				resolve({ viewData: data.list });
			} else {
				this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
			}
		}, error => {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
		})
	}
	//点击客户号跳转
	toCustView(data) {
		if (data.relationKey == 'party') {
			this.commfunc.setSessionData('custNo', data.partyOrGroupNo);
			window.open('#/pages/custom-view/general', '_blank')
		} else if (data.relationKey == 'partyGroup') {
			window.open(`#/pages/tzb/custom/custom-group/custom-group-view?groupId=${data.partyOrGroupNo}`, '_blank')
		}
	}


	// ---------------以下保留之前代码-------------------------------
	// 进入审批
	toDisposeSchedule(item) {
		this.commfunc.doDeal(item);

	}

	//事项小类动态业务数据展示
	/**
	 * 
	 * @param queryType 查询类型：0-查询条件;1-表头
	 * @param taskCategoryId 流程模板编号
	 * @param nodeCode 流程节点编号
	 */
	tableTitleListonly: any = [];
	itemType: any;
	queryTaskNoteMessage(queryType, taskCategoryId) {
		let param = {
			queryType: queryType,
			taskCategoryId: taskCategoryId
		}
		this.httpService.queryTaskNoteMessage(param).subscribe(data => {
			if (data.returnCode.code = 'success') {
				//查询条件
				if (queryType == '1') {
					this.queryList = [];
					if (data.taskNoteMessageList && data.taskNoteMessageList.length > 0) {
						let item = data.taskNoteMessageList[0]['textMsg'];
						for (let i in item) {
							let obj = {};
							if (item.hasOwnProperty(i)) {
								obj['i'] = item[i].split('|')[0];
								obj['key'] = '';
								obj['label'] = item[i].split('|')[1];
								this.queryList.push(obj)
								this.queryList = _.clone(this.queryList)
							}
						}
					} else {
						this.queryList = [];
					}
					this.queryList.push(
						{ i: "startUserId", key: "", label: "发起人" },
						{ i: "createDateFrom", key: "", label: "创建起始日期" },
						{ i: "createDateTo", key: "", label: "创建截止日期" }
					)
					console.log(this.queryList);
				}
				//查询表头
				if (queryType == '2') {
					this.tableTitleListonly = [];
					this.tableTitleList = [];
					let item;
					if (this.itemType && this.itemType == 'SXSX') {
						this.tableTitleListonly.push({ field: "littleClassName", header: "事项名称" });
					}
					item = _.clone(data.taskNoteMessageList[0]['textMsg']);
					for (let i in item) {
						let obj = {};
						if (item.hasOwnProperty(i)) {
							obj['field'] = item[i].split('|')[0];
							obj['header'] = item[i].split('|')[1];
							this.tableTitleList.push(obj)
						}
					}
					console.log(this.tableTitleList);
				}
				this.paramNUM = '2';
			}
		})
	}
}
