import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';//事项类型的接口
import { ActivatedRoute, Router } from '@angular/router';
import { BtnPageBean } from '../bean/btnPage.bean';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { TZB_HTTP_XIN, TZB_HTTP_XIN_C } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ApprovePageBean } from './bean/approvePage.bean';
import * as $ from 'jquery';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { DomSanitizer } from '@angular/platform-browser';
/**
 * 审批事项
 */
@Component({
  selector: 'app-approve-page',
  templateUrl: './approve-page.component.html',
  styleUrls: ['./approve-page.component.scss',
    './../menu-tree-ul.component.scss'
  ],
  providers: [CusOperationHttpService, CustomeHttpService, Commfunc]
})
export class ApprovePageComponent implements OnInit, OnChanges {
  disinNav: boolean = false;
  inNav: any;
  Furl: string;
  listUrl: any;
  list: any = [];//接收后台数据
  startTime: Date = null;
  endTime: Date = null;
  ch: any = CALENDAR_CN;//讲英文转化为中文
  first: number;
  userId: string = '';
  //提示信息
  msgs: Message[] = [];
  menuList: any;
  approvePageBean: ApprovePageBean = new ApprovePageBean();
  //获取所有码值
  code: any;
  ulData: any = [];
  general: boolean = false;
  constructor(
    public router: Router,
    public routeInfo: ActivatedRoute,
    public cusOperationHttpService: CusOperationHttpService,
    public customeHttpService: CustomeHttpService,
    public activatedRouter: ActivatedRoute,
    public commfunc: Commfunc,
    private sanitizer: DomSanitizer
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
  }

  ngOnInit() {
    //判断是否是总行或者集团
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let a = commonHeader.extParam.operatingLevel;
    if (a == 'GROUP' || a == 'GENERAL_BANK' || a == 'GENERAL_BANK_DT') {
      this.general = true;
    } else {
      this.general = false;
    }
    let session = window.sessionStorage;
    if (session.getItem('bussinessWhere')) {
      let bussinessWhere = session.getItem('bussinessWhere');
      if (bussinessWhere.indexOf('startUserId') != -1) {
        let t = bussinessWhere.split('=');
        this.approvePageBean.startUserId = t[1];
      } else {
        this.approvePageBean.bussinessWhere = bussinessWhere;
      }
    }

    this.loanApprCountByTypeQry();
    
    if (this.commfunc.getSessionDataCH('markFlag')) {
      this.commfunc.clearSession('markFlag');
    }
    if (this.commfunc.getSessionDataCH('taskId')) {
      this.commfunc.clearSession('taskId');
    }
    if (this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')) {
      this.commfunc.clearSession('bpmQueryTaskOnlyList');
    }
  }
  ngOnChanges() {
    this.ngOnInit()
  }
  //信贷-审批事项
  loanApprCountByTypeQry() {
    let param = {};
    let ob = this.customeHttpService.loanApprCountByTypeQry(param);
    ob.subscribe(data => {
      let tempArr = [];
      if (data.resultList && data.resultList.length > 0) {
        data.resultList.forEach(item => {
          let tempObj = {};
          tempObj['scheduleTypeId'] = item.scheduleTypeId;
          tempObj['littleClassName'] = item.scheduleName;
          tempObj['sysCount'] = item.countNum;
          tempObj['url'] = item.url;
          tempObj['flag'] = item.flag;
          tempArr.push(tempObj)
        });
      }
      this.queryNewList(tempArr)
    })
  }
  nodata: boolean = false;
  maskLayer: boolean = true;
  // disapprove: boolean = false;
  // 查询待办列表（汇总）
  queryNewList(datas?) {
    this.nodata = false;
    this.maskLayer = true;
    this.approvePageBean.taskPoolFlag = '3';
    this.approvePageBean.itemType = 'KHLX';
    this.approvePageBean.itemCtrl = '1';
    this.approvePageBean.endFlag = '0';
    this.approvePageBean.userId = this.userId;
    this.approvePageBean.nodeType = '0'; // 待办
    this.customeHttpService.bpmQueryUnDoTaskCountGroup(this.approvePageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list = data.pageData.result;
        // if (this.list.length > 0) {
        //   this.disapprove = true;
        // }
        if (this.list.length > 0 || (datas && datas.length > 0)) {
          if (datas && datas.length > 0) {
            this.approve(this.list, datas)
          } else {
            this.approve(this.list)
          }
        } else {
          this.nodata = true;
          $('#appR').height(700);
          this.maskLayer = false;
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  approve(list, datas?) {
    let approvelist = []
    for (let i = 0; i < this.list.length; i++) {
      let element = this.list[i];
      let filterResult = approvelist.filter(x => x.bigClassNo == element['bigClassNo']);
      let exist = filterResult && filterResult.length;
      let temp = null;
      if (exist) {
        temp = {
          littleClassName: element['littleClassName'],
          littleClassNo: element['littleClassNo'],
          sysCount: element['sysCount'],
          timeOutCount: element['timeOutCount'],//超时
          urgentCount: element['urgentCount'],//紧急
          systemCode: element['systemCode']
        }
        filterResult[0].childpage.push(temp);
        filterResult[0].bigsysCount = filterResult[0].bigsysCount + element['sysCount']
      } else {
        temp = {
          bigClassName: element['bigClassName'],
          bigClassNo: element['bigClassNo'],
          bigsysCount: element['sysCount'],
          bigtimeOutCount: element['timeOutCount'],//大类超时总数
          bigurgentCount: element['urgentCount'],//大类紧急总数
          childpage: [
            {
              littleClassName: element['littleClassName'],
              littleClassNo: element['littleClassNo'],
              sysCount: element['sysCount'],
              timeOutCount: element['timeOutCount'],//超时
              urgentCount: element['urgentCount'],//紧急
              systemCode: element['systemCode']
            }
          ]
        }
        approvelist.push(temp);
      }
    }
    // for (let t = 0; t < approvelist.length; t++) {
    //   const e = approvelist[t];
    //   if (e.bigClassNo == 'SXSX') {
    //     let pag = {
    //       littleClassName: '所有授信事项',
    //       littleClassNo: e['bigClassNo'],
    //       sysCount: e['bigsysCount'],
    //       timeOutCount: e['bigtimeOutCount'],
    //       urgentCount: e['bigurgentCount'],
    //       systemCode: 'SXSX:授信事项;SXSX:授信事项',
    //       biaoji: '手工加全部',
    //     }
    //     e.childpage.unshift(pag)
    //     break;
    //   }
    // }
    if (datas) {
      let loandate = [];
      let loandate1 = null;
      if (datas && datas.length > 0) {
        loandate1 = {
          bigClassName: '信贷系统',
          bigsysCount: 0 * 1,
          childpage: []
        }
        datas.forEach(e => {
          if (e.index != 0) {
            loandate1.bigsysCount = loandate1.bigsysCount * 1 + e.sysCount * 1
          }
          loandate1.childpage.push({
            littleClassName: e.littleClassName,
            sysCount: e.sysCount,
            scheduleTypeId: e.scheduleTypeId,
            url: e.url,
            orgtype: e.flag
          })
        });
        loandate.push(loandate1);
      }
      approvelist = approvelist.concat(loandate);
    }
    console.log(approvelist)
    this.ulData = approvelist;
    let session = window.sessionStorage;
    if (session.getItem('workliuCode')) {
      let f = session.getItem('workliuCode');
      let d = f.split('&');
      if (!approvelist[d[0]]) {
        d[0] = '0'; d[1] = '0';
      } else if (!approvelist[d[1]]) {
        d[1] = '0'
      }
      let a = d[0] + '&' + d[1];
      session.setItem('workliuCode', a);
      this.inNav = approvelist[d[0]].childpage[d[1]];
    } else {
      this.inNav = approvelist[0].childpage[0];
    }
    setTimeout(() => {
      this.start();
      this.maskLayer = false;
    }, 1500);
    this.disinNav = true;
  }
  HTTPIPDress: any;
  navclick(item) {
    if (this.commfunc.getSessionDataCH('workliuCode')) {
      this.commfunc.clearSession('workliuCode');
      this.commfunc.clearSession('workliuData');
    }
    if (this.commfunc.getSessionDataCH('custdata')) {
      this.commfunc.clearSession('custdata');
    }
    for (let t = 0; t < this.ulData.length; t++) {
      for (let j = 0; j < this.ulData[t].childpage.length; j++) {
        if (this.ulData[t].childpage[j].littleClassNo == item.littleClassNo) {
          let session = window.sessionStorage;
          let a = t + '&' + j;
          let b = JSON.stringify(item);
          session.setItem('workliuCode', a);
          session.setItem('workliuData', b);
          break;
        }
      }
    }
    if (item.url && item.url != '') {
      if (this.commfunc.getSessionDataCH('workliuCode')) {
        this.commfunc.clearSession('workliuCode');
        this.commfunc.clearSession('workliuData');
      }
      //信贷工作任务处理
      let timestamp = new Date().getTime();
      let target = "custLinkForm" + timestamp;

      //审批事项中的信贷
      let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      let userId = user.userId;
      let targetId = user.targetId;
      let params = {
        userId: this.userId,
        sysCode: '122',
        casServerLoginUrl: 'http://10.1.39.140:8011/searchPaymentTool',
        serverName: 'ywsn',
        validateType: '0',
        extraInfo: 'ywsn',
        encryptType: 'HS256',
      }
      this.customeHttpService.tokenApply(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          let token = data.tokenCode;
          let url = item.url + '&suid=' + userId + '&token=' + token;
          let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
          let closeOnLoseFocus = 'closeOnLoseFocus';
          this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
      })
    }
    this.inNav = _.clone(item);
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  start() {
    $(document).ready(function () {
      // if (this.disapprove) {
      let session = window.sessionStorage;
      if (session.getItem('bussinessWhere')) {
        $('.menu_list ul li').eq(0).children('.div1').show().children('.zcd').eq(0).addClass('removes');
      } else if (session.getItem('workliuCode')) {
        let f = session.getItem('workliuCode');
        let d = f.split('&');
        $('.menu_list ul li').eq(d[0]).children('.div1').show().children('.zcd').eq(d[1]).addClass('removes')
      } else {
        $('.menu_list ul li').eq(0).children('.div1').show().children('.zcd').eq(0).addClass('removes');
      }
      // }else{
      //   $('.menu_list ul li').eq(0).children('.div1').show();
      // }
      //绑定元素点击事件
      $(".menu_list ul li").click(function () {
        //判断对象是显示还是隐藏
        if ($(this).children(".div1").is(":hidden")) {
          //表示隐藏
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(180deg)' });
            //如果当前没有进行动画，则添加新动画
            $(this).children(".div1").animate({
              height: 'show'
            }, 200)
              //siblings遍历div1的元素
              .end().siblings().find(".div1").hide(200);
            $(this).siblings().children(".xiala").css({ 'transform': 'rotate(360deg)' });
            // $(this).children('.div1').children('.zcd').siblings().removeClass("removes");
            // $(this).siblings().children('.div1').children('.zcd').removeClass("removes");
            // $(this).children('.div1').children('.zcd').eq(0).addClass('removes');
            // let aname = $(this).children('.div1').children('.zcd').eq(0)[0].title;
            // console.log(aname, 'rrrrrrrr')
          }
        } else {
          //表示显示
          if (!$(this).children(".div1").is(":animated")) {
            $(this).children(".xiala").css({ 'transform': 'rotate(360deg)' });
            $(this).children(".div1").animate({
              height: 'hide'
            }, 200)
              .end().siblings().find(".div1").hide(200);
          }
        }
      });

      //阻止事件冒泡，子元素不再继承父元素的点击事件
      $('.div1').click(function (e) {
        e.stopPropagation();
      });

      //点击子菜单为子菜单添加样式，并移除所有其他子菜单样式
      $(".menu_list ul li .div1 .zcd").click(function () {
        //设置当前菜单为选中状态的样式，并移除同类同级别的其他元素的样式
        $(this).addClass("removes").siblings().removeClass("removes");
        //遍历获取所有父菜单元素
        $(".div1").each(function () {
          //判断当前的父菜单是否是隐藏状态
          if ($(this).is(":hidden")) {
            //如果是隐藏状态则移除其样式
            $(this).children(".zcd").removeClass("removes");
          }
        });
      });
    });
  }
}
